@import '../../constants/variable';

.c_tokenList
{
    height: 400px;
    background-color: $bg-left;
    font-size: 14px;
    border-radius: 4px;

    .search
    {
        position: relative;
        padding: $div-gap;

        input
        {
            padding: 0;
            width: calc(100% - 12px);
            border: 1px solid $font-gray;
            background-color: transparent;
            height: 24px;
            line-height: 24px;
            outline: none;
            padding-left: 10px;
            color: $font-purple;
            -webkit-text-fill-color: $font-gray;

            &.no-result
            {
                border: 1px solid $main-red;
            }

            &:focus
            {
                -webkit-text-fill-color: #fff;
                border: 1px solid #fff;
            }

            &.no-result:focus
            {
                border: 1px solid $main-red;
            }
        }

        img
        {
            position: absolute;
            right: 26px;
            width: 24px;
            height: 20px;
            width: 20px;
            top: 50%;
            transform: translateY(-50%);
            cursor: pointer;
        }
    }

    .option
    {
        display: flex;
        // margin-top: 16px;
        color: $font-gray;
        padding: 0 $div-gap;
        font-size: 12px;
        > div
        {
            margin-right: 10px;
            cursor: pointer;
        }

        .active
        {
            color: #fff;
            border-bottom: 2px solid #fff;
        }
    }

    .list-title
    {
        color: $font-gray;
        display: grid;
        grid-template-columns: 10% 30% 30% 30%;
        text-align: right;
        padding: 8px;
        font-size: 12px;
    }

    .list
    {
        list-style: none;
        color: #fff;
        padding: 0;
        margin: 0;
        font-size: 12px;
        overflow: auto;
        height: 288px;
        padding: 0 8px;

        &::-webkit-scrollbar
        {
            display: none;
        }

        li
        {
            display: grid;
            grid-template-columns: 10% 30% 30% 30%;
            padding: 8px;
            cursor: pointer;

            &:hover
            {
                background-color: $bg-right;
            }

            > div
            {
                display: flex;
                align-items: center;
                justify-content: flex-end;
                
                &:first-child
                {
                    text-align: left;
                    justify-content: flex-start;
    
                    i
                    {
                        cursor: pointer;
    
                        &.checked
                        {
                            color: #f8e71b;
                        }
                    }
                }

                &:nth-child(2)
                {
                    justify-content: flex-start;
                }
            }

            .desc
            {
                color: $font-gray;
                font-size: 11px;

                span
                {
                    font-size: 15px;
                    color: #fff;
                    margin-right: 4px;
                }
            }

            .ratio
            {
                &.green
                {
                    color: $main-green;
                }

                &.red
                {
                    color: $main-red;
                }
            }
        }
    }

    @include rwd(ipad)
    {
        width: 100%;
    }
}
