@import '../../constants/variable';

.c_header
{
    background-color: $header-bg-color;
    color: #fff;
    padding: 0 34px 0 40px;
    font-size: 16px;
    @include rwd(ipad)
    {
        padding: 0 10px;
    }

    h1
    {
        font-size: 1em;
        margin: 0;
        margin-right: $div-gap;
        
        a
        {
            color: #fff;
            text-decoration: none;
            display: flex;
            align-items: center;
        }

        > span
        {
            opacity: 0;
            margin-left: 20px;
            transition: .3s;

            @include rwd(ipad)
            {
                display: none;
            }
        }

        &:hover
        {
            > span
            {
                opacity: .8;
            }
        }
    }

    .beta
    {
        position: absolute;
        width: 60px;
        left: 0;
        top: 0;
    }

    .small-title
    {
        font-size: .75em;
        margin-left: 2px;
        font-weight: 300;
        padding-top: 2px;
    }

    .header-desktop
    {
        display: flex;

        &::after
        {
            content: '';
            position: absolute;
            background-color: rgba(0, 0, 0, .5);
            height: calc(100% - 48px);
            width: 100%;
            top: 48px;
            left: 0;
            transition: .3s;
            z-index: -1;
            opacity: 0;
        }

        &.block::after
        {
            opacity: 1;
            z-index: 999;
        }
    }

    .header-mobile
    {
        display: none;
        height: 36px;
        
        @include rwd(ipad)
        {
            width: 100%;
            display: flex;
            align-items: center;

            .logo-mobile
            {
                margin-left: 30px;
                img
                {
                    width: 30px;
                    height: 30px;
                }
            }

            .token-mobile
            {
                margin-left: 40px;
                font-size: 20px;
            }

            .ratio
            {
                margin-left: 20px;
                font-size: 20px;
            }
        }
    }

    .logo
    {
        margin-right: 3px;
        display: flex;
        align-items: center;

        img
        {
            width: 48px;
            height: 48px;
        }
    }

    .empty
    {
        flex: auto;
    }

    .controls
    {
        margin-right: 20px;

        a
        {
            color: #fff;
            font-size: 0.8em;
            text-decoration: none;
            transition: .3s;
            display: flex;
            align-items: center;

            @include rwd(ipad)
            {
                span
                {
                    display: none;
                }
            }
        }

        &:hover
        {
            a
            {
                color: $font-purple;
            }
        }

        i
        {
            margin-right: 4px;
        }
    }

    .language
    {
        width: 64px;
        padding: 3px 8px;
        height: 18px;
        z-index: 100;
        cursor: pointer;
        position: relative;
        border: 1px solid $font-gray;
        font-size: .75em;
        color: #d9d9d9;

        > div
        {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

        .lang-list
        {
            display: block;
            position: absolute;
            top: 25px;
            left: -1px;
            z-index: -99;
            opacity: 0;
            transition: .5s;
            background-color: rgba(150, 150, 150, .9);
            widows: auto;
            pointer-events: none;
            padding-bottom: 5px;

            > div
            {
                padding: 3px 8px;
                width: 66px;
                text-align: center;
                border-top: 1px solid $font-gray;
                &:first-child
                {
                    border: none;
                }
                &:hover
                {
                    background-color: $main-gray;
                }
            }
        }

        &:hover
        {
            .lang-list
            {
                opacity: 1;
                transition: .5s;
                pointer-events: auto;
            }
        }
    }

    .menu
    {
        cursor: pointer;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-left: 34px;
        
        span
        {
            width: 16px;
            height: 2px;
            border-radius: 1px;
            background-color: #ffffff;
            display: block;
            margin-bottom: 5px;
        }

        @include rwd(ipad)
        {
            margin-left: 22px;
            margin-right: 15px;
        }
    }

    .control
    {
        width: 240px;
        position: fixed;
        top: 0;
        height: 100%;
        right: 0;
        background-color: $bg-left;
        z-index: 9999;
        padding-top: 21px;
        transition: .4s;
        transform: translateX(240px);
        display: flex;
        flex-direction: column;

        &.show
        {
            transform: translateX(0);
            &::after
            {
                content: '';
            }
        }

        > div
        {
            > div
            {
                display: flex;
                cursor: pointer;
                
                > a
                {
                    padding: 14px 40px;
                    width: 100%;
                }

                > i
                {
                    margin: 14px 40px;
                    margin-top: 16px;
                    color: #ab4af2;
                }

                &:hover
                {
                    background-color: #000;

                    > a
                    {
                        color: #fff;
                    }
                }

            }

            &.group
            {
                > div
                {
                    &:last-child
                    {
                        border-bottom: solid 2px #4a4a4a;
                    }
                }
            }

            .active
            {
                pointer-events: none;

                a
                {
                    color: #ab4af2;
                }
            }

            a
            {
                text-decoration: none;
                color: $font-gray;
            }
        }

        .version
        {
            margin-top: auto;
            padding-bottom: 21px;
        }
    }

    .cover
    {
        background-color: rgba(0, 0, 0, .5);
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        transition: .5s;
        z-index: -1;

        &.show
        {
            opacity: 1;
            z-index: 999;
        }
    }

    .summary
    {
        display: flex;
        position: relative;

        > div
        {
            padding: 0 $div-gap;
            border-left: 1px #323955 solid;
            display: flex;
            align-items: center;

            &:first-child
            {
                border: none;
            }
            
        }

        .desc
        {
            cursor: pointer;
            position: relative;
            
            > div
            {
                display: flex;
                flex-direction: column;

                span
                {
                    font-size: 11px;
                    color: $font-gray;

                    &:first-child
                    {
                        font-size: 16px;
                        color: #fff;
                        margin-bottom: 2px;
                    }
                }
            }

            i
            {
                margin-left: 10px;
            }
        }

        .rate
        {
            span
            {
                font-size: 18px;
                color: #fff;

                &:last-child
                {
                    font-size: 11px;
                    margin-left: 8px;
                }

                &.up
                {
                    color: $main-green;
                }
                
                &.down
                {
                    color: $main-red;
                }
            }
        }

        .infor
        {
            font-size: 11px;

            > div
            {
                margin-right: 30px;
                text-align: right;
            }

            p
            {
                color: $font-gray;
                margin: 0;
                margin-bottom: 4px;
            }

            span
            {
                color: #fff;
            }
        }

        @include rwd(ipad)
        {
            display: none;
        }
    }

    .token
    {
        opacity: 0;
        top: 50px;
        left: -80px;
        z-index: -1;
        position: absolute;
        transition: .7s;
        padding: 0;

        &.show
        {
            opacity: 1;
            top: 66px;
            z-index: 9999;
        }

        &::before
        {
            content: '';
            position: absolute;
            height: 0;
            width: 0;
            top: -20px;
            left: 50%;
            transform: translate(-50%, 0);
            border-left: 30px solid transparent;
            border-right: 30px solid transparent;
            border-bottom: 30px solid $bg-left;
        }
    }

    .contract
    {
        position: relative;
        cursor: pointer;
        font-size: 12px;
        padding: 0 $div-gap;
        border-left: 1px #323955 solid;
        border-right: 1px #323955 solid;
        display: flex;
        align-items: center;

        p
        {
            margin: 0 12px;
        }

        > div
        {
            opacity: 0;
            z-index: -1;
            position: absolute;
            width: 408px;
            transition: .5s;
            right: 0;
            top: 58px;
            background-color: $bg-left;

            > div
            {
                padding: 12px;
                display: flex;

               
                .link
                {
                    display: flex;
                    color: #fff;
                    text-decoration: none;

                    > div:first-child
                    {
                        flex: 4;
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        margin-right: 1%;
                    }

                    > div:last-child
                    {
                        flex: 6;
                    }
                }
                
                
                &:hover
                {
                    background-color: $bg-right;
                }

                &:first-child
                {
                    border-bottom: 1px $font-gray solid;
                    pointer-events: none;
                }
            }

            &.open
            {
                opacity: 1;
                z-index: 9999;
                top: 48px;
            }
        }
    }

}