$main-green: #27ca7b;
$main-red: #f44173;
$main-gray: #5e6c75;
$main-orange: #E7A977;

$bg-black: #191e32;
$bg-left: #202841;
$bg-right: #0e1223;
$bg-right-title: #0b0d16;
$bg-left-title: #0a1229;

$font-gray: #8a8a8a;
$font-title: #d9d9d9;
$font-purple: #7166fc;
$font-green: #77f441;

$modal-gray: #d9d9d9;

$table-gray: #99a7af;
$modal-black: rgba(0, 0, 0, .7);
$header-bg-color: #12172a;
$div-gap: 16px;
$phone-large: 768px;
$ipad-large: 768px;
$desktop-large: 1024px;

@mixin rwd($media) {
    @if $media == phone {
        @media (max-width: $phone-large) { @content; }
    }

    @if $media == ipad {
        @media (max-width: $ipad-large) { @content; }
    }

    @if $media == desktop {
        @media (max-width: $desktop-large) { @content; }
    }
}

@mixin container
{
    position: relative;
    padding-bottom: 64px;
    min-height: calc(100vh - 64px);
}

@mixin button
{
    border-radius: 4px;
    font-size: 15px;
    border: solid 1px $font-purple;
    color: $font-purple;
    border-radius: 4px;
    padding: 6px 7px;
    text-align: center;
    cursor: pointer;
    position: relative;
    margin-left: 16px;

    span
    {
        z-index: 3;
        position: relative;
        transition: .2s;
        font-size: 12px;
    }

    &::after
    {
        position: absolute;
        content: '';
        opacity: 0;
        transition: .2s;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: linear-gradient(292deg, #4032f3, #9822ee);
        border-radius: 4px;
    }

    &:first-child
    {
        margin-left: 0;
    }

    &:hover
    {
        color: #fff;
        // background-image: linear-gradient(292deg, #4032f3, #9822ee);
        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.4);
        border-color: transparent;

        span
        {
            color: #fff;
            // font-weight: 300;
        }

        &::after
        {
            opacity: 1;
        }
    }
    
    &.active
    {
        color: #fff;
        background-image: linear-gradient(292deg, #7166fc, #ab4af2);
    }
}

@mixin prompt
{
    position: relative;
    display: flex;
    align-items: center;
    font-size: 12px;

    > img
    {
        cursor: pointer;

        &:hover
        {
            & + div
            {
                opacity: 1;
                z-index: 100;
            }
        }
    }

    > div
    {
        color: $font-gray;
        border: 1px solid $font-gray;
        border-radius: 5px;
        background-color: #000;
        padding: 3px 5px;
        position: absolute;
        left: 100%;
        top: 50%;
        opacity: 0;
        z-index: -1;
        transform: translate(10px, -50%);
        // white-space: nowrap;
        min-width: 200px;
        line-height: 14px;
    }

    @include rwd(ipad)
    {
       display: none;
    }
}

@mixin title($bgc)
{
    color: $font-title;
    padding: 10px 16px;
    height: 20px;
    line-height: 20px;
    background-color: $bgc;
    border-radius: 4px 4px 0 0;
    font-size: 14px;
}

@mixin toggle($num)
{
    position: absolute;
    top: 20px;
    z-index: 99;
    width: 100%;
    right: 0;
    background-color: $bg-black;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.5);
    color: $font-gray;
    border-radius: 2px;
    transition: .3s;
    // height: calc($num * 32px);
    overflow: hidden;
    padding: 8px 0;

    > div
    {
        padding: calc(#{$div-gap} / 2);
        height: 16px;

        &:hover
        {
            background-color: #000;
            color: $font-purple;
        }
    }

    &.close
    {
        height: 0;
        padding: 0;
    }
    
}

@mixin nav
{
    padding: 20px 40px;
    color: $font-purple;
    display: flex;

    a
    {
        color: $font-purple;
        text-decoration: none;
    }

    img
    {
        margin: 0 6px;
    }

    span:last-child
    {
        color: #fff;
    }
}

